export const api = ({ method, args = {}, freeze=true  }) =>
    new Promise((resolve, reject) =>
        frappe.call({ method, args, callback: resolve, freeze })
    );


    export const createPatientAllergy = (payload) =>
    api({
        method: "clinical.api.notes.patient_allergy.execute",
        args:{payload},
    }).then(({ message }) => message);

    export const patientAllergyAddItem = (payload) =>
    api({
        method: "clinical.api.notes.patient_allergy.add_to_patient_allergy",
        args:{payload},
        freeze:false,
    }).then(({ message }) => message);


    export const patientAllergyRemoveItem = (name) =>
    api({
        method: "clinical.api.notes.patient_allergy.remove_item_from_patient_allergy",
        args:{name},
    }).then(({ message }) => message);

    export const checkPatientAllergy = (patient) =>
    api({
        method: "clinical.api.notes.patient_allergy.check_if_exits",
        args:{patient},
    }).then(({ message }) => message);

    export const patientAllergySubmit = (name) =>
    api({
        method: "clinical.api.notes.patient_allergy.submit_patient_allergy",
        args:{name},
        freeze:false,
    }).then(({ message }) => message);


    export const patientAllergyDelete = (name) =>
    api({
        method: "clinical.api.notes.patient_allergy.delete_patient_allergy",
        args:{name},
        freeze:false,
    }).then(({ message }) => message);