import { api } from "./meta.js";
export const getList = (payload) =>
    api({
        method: "mtrh_dev.api.supplier-portal.base.base.get_list",
        args: { payload },
    });

export const getDoctypeByName = (payload) =>
    api({
        method: "clinical.api.forms.form_builder.get_doctype_by_name",
        args: { payload },
    });

export const getSupplier = (username) =>
    api({
        method: "mtrh_dev.mtrh_dev.get_linked_supplier",
        args: { username },
    });

export const createDoctype = (payload) =>
    api({
        method: "mtrh_dev.api.supplier-portal.base.base.save_form_data",
        args: { payload },
    });
