export const api = ({ method, args = {}, freeze=true }) =>
    new Promise((resolve, reject) =>
        frappe.call({
            method,
            args,
            freeze,
            callback: ({ message }) => resolve(message),
            error: reject,
        })
    );

export const getPatientInfo = (patient_name) =>
    api({
        method: "clinical.api.patients.get_patient_details",
        args: patient_name,
    });

export const wavePatientRegistrationFee = (patient, reasons) =>
    api({
        method: "clinical.api.patients.waive_patient_registration",
        args: { patient, reasons },
    });

export const createAppointment = ({
        patient,
        service_unit,
        practitioner,
        department,
        appointment_date,
        appointment_time,
        referring_practitioner,
    }) =>
    api({
        method: "clinical.api.patient_encounter.patient_encounter.create_appointment",
        args: {
            patient,
            service_unit,
            practitioner,
            department,
            appointment_date,
            appointment_time,
            referring_practitioner,
        },
    });
