export const api = ({ method, args = {}, freeze=true  }) =>
    new Promise((resolve, reject) =>
        frappe.call({ method, args, freeze, callback: resolve })
    );


export const itemStockBalanceWarehouse = ({
    item_code,warehouse
}) =>
api({
    method: "clinical.api.warehouse.warehouse.get_item_stock_warehouse_quantities",
    args: {
        item_code,warehouse
    },
}).then(({ message }) => message)