export const api = ({ method, args = {}, freeze = true }) =>
    new Promise((resolve, reject) =>
        frappe.call({
            method,
            args,
            freeze,
            callback: ({ message }) => resolve(message),
            error: reject,
        })
    );

export const getPatient = ({ patient_name }) =>
    api({
        method: "billing.billing.api.accounts.patients.get_patient",
        args: {
            patient_name,
        },
    });

