                doctype: "Sales Order",
                docname: salesOrderName,
                on_success(file_doc) {
                    approvePreAuth(salesOrderName).then((res) => {
                        transitionSalesOrderToInvoice({ sales_order_name: res }).then(() => {
                            frappe.show_alert({
                                message: `Successfully authorized`,
                                indicator: 'green'
                            }, 15);
                        })

