export default {
    getPatients: (state) => state.patients,
    getPatient: (state) => (patientName) =>
        state.patients.find((patient) => patient.name == patientName),
    getSelectedPatient: (state) => state.selectedPatient,
    getSelectedPatientDetails: (state) => state.selectedPatientDetails || {},
    getTimelineData: (state) => state.timelineData,
};