export const api = ({ method, args = {}, freeze = true }) =>
    new Promise((resolve, reject) =>
        frappe.call({
            method,
            args,
            freeze,
            callback: ({ message }) => resolve(message),
            error: reject,
        })
    );

export const getPatientDashboardData = ({ patient, customer}) =>
    api({
        method: "billing.billing.api.customer.dashboard.get_balance_dashboard",
        args: {
            patient,
            customer
        },
    });

