export default {
    getSuppliers: (state) => state.suppliers,
    getSupplier: (state) => (supplierName) =>
        state.suppliers.find((supplier) => supplier.name == supplierName),
    getSelectedSupplier: (state) => state.selectedSupplier,
    getSelectedSupplierDetails: (state) => state.selectedSupplierDetails || {},
    getTimelineData: (state) => state.timelineData,
};