export const api = ({ method, args = {}, freeze=true }) =>
    new Promise((resolve, reject) =>
        frappe.call({
            method,
            args,
            freeze,
            callback: ({ message }) => resolve(message),
            error: reject,
        })
    );

export const apiPlain = ({ method, args = {} }) =>
    new Promise((resolve, reject) =>
        frappe.call({
            method,
            args,
            callback: (message) => resolve(message),
            error: reject,
        })
    );

export const apix = ({ method, args = {} }) =>
    new Promise((resolve, reject) =>
        frappe.call({
            method,
            args,
            callback: ({ results }) => resolve(results),
            error: reject,
        })
    );

export const getDoctypeWithMeta = (payload) =>
    api({
        method: "clinical.api.doctype.meta.get_doctype_with_meta",
        args: { payload },
    });

export const getDoctypeWithMetaSlim = (payload) =>
    api({
        method: "clinical.api.doctype.meta.get_doctype_with_meta_slim",
        args: { payload },
    });

export const getDoctypeRenderSettings = (payload) =>
    api({
        method: "mtrh_dev.mtrh_dev.doctype.renderer_settings.renderer_settings.get_render_settings",
        args: { payload },
    });

export const updateDoctype = (payload) =>
    api({
        method: "frappe.desk.form.save.savedocs",
        args: { doc: payload, action: "Update" },
    });

export const addPatientToQueue = (appointment) =>
    api({
        method: "billing.billing.api.patient.patient_balance.add_emergency_patient_to_queue",
        args: { appointment },
    });


export const cancelDoctype = (doctype, name) =>
    api({
        method: "frappe.desk.form.save.cancel",
        args: { doctype, name },
    });

export const getDoctype = (doctype, name) =>
    apiPlain({
        method: "frappe.desk.form.load.getdoc",
        args: { doctype, name },
    });

export const searchDoctype = (payload) =>
    apix({
        method: "frappe.desk.search.search_link",
        args: { payload },
    });