export const api = ({ method, args = {}, freeze=true }) =>
    new Promise((resolve, reject) =>
        frappe.call({ method, args, freeze, callback: resolve })
    );

export const getPatientInvoices = ({
        patient_number = "",
        appointment_number = "",
        inpatient_record = ""
    }) =>
    api({
        method: "clinical.api.patient_accounts.get_patient_invoice",
        args: {
            patient_number,
            appointment_number,
            inpatient_record
        },
    }).then(({ message }) => message);

